@import "estilo_extra";
/*Definicion de variables*/

$bgcolor: lightblue;
$textcolor: darkblue;
$fontsize: 18px;

$una-variable:#77ff22;
$miFont: Helvetica, sans-serif;
$miColor: red;
$miFontSize: 22px;
$miWidth: 680px;

$txt: "ABCxyz";

@mixin nombre-cualquiera {
    color: brown;
    font-size: 25px;
    font-weight: bold;
    border: 1px solid blueviolet;
}

@mixin otro-mixin {

}

@mixin bordeador ($coloreishon, $ancho){
    border: $ancho solid $coloreishon;
    color: $coloreishon;
}

/*mixin con argumentos (valores por defecto)*/
@mixin bordeador2($coloreishon: blue, $ancho: 2px){
    border: $ancho solid $coloreishon;
    color: $coloreishon;
}

@mixin super_mixin($bgcolor: white, $color: black, $border-color: red, $ancho:10px){
    background-color: $bgcolor;
    border: $ancho solid $color;
    color: $color;
}

@mixin estilizado-especial{
    @include nombre-cualquiera;
    @include otro-mixin;
}

@mixin transformacion($propiedad){
    -webkit-transform: $propiedad;
    -ms-transform: $propiedad;
    transform: $propiedad;
}

/*Uso de variables*/
body {
    background-color: $bgcolor;
    color: $miColor;
    font-size: $miFontSize;
    font-family: $miFont;
    font: {
        size: $miFontSize;
        family: $miFont;
        weight: bold;
    }

    text: {
        align: center;
        transform: uppercase;
        overflow: hidden;
    }
}

#contenedor {
    width: $miWidth;
}

h1{
    $miColor: green !global;
    $abc:30px;
    color: $miColor;
}
p {
    @include nombre-cualquiera;
    padding: 10px;
}

/*Uso de anidamiento*/
nav {
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
    }
    li {
        display:inline-block;
        @include bordeador(orange , 15px);
    }
    a {
        @include nombre-cualquiera;
        display: block;
        padding: 3px 30px;
        text-decoration: none;
    }
}

div {
    @include bordeador(blue, 5px);
    
}

p{
    a {
        @include super-mixin($ancho: 3px, $color: red, $border-color: orange);  
    } 
}

.caja {
    @include transformacion(rotate(45deg));
    background-color: yellow;
    border: 5px solid yellow;
    color: black;
}

.boton-simple {
    border: none;
    padding: 15px 30px;
    text-align: center;
    font-size: 15px;
    cursor: pointer;
}

.boton-alerta {
    @extend .boton-simple;
    background-color: red;
}

.boton-submit {
    @extend .boton-simple;
    background-color:greenyellow;
    color: orangered;
}

/** Funciones en SASS*/

/*cadenas (string)
texto

"sdfds d 56 545"

"k"

" "

""

NULL

*/
$txt: "Pepe";

div {
    $cad: quote(Arial);/*"Arial"*/

    $cad: unquote($string: "Arial");
    /*Veamos el resultado con una condicional*/
    font-family: $cad, Helvetica, sans-serif;

    /*Para obtener el indice de una subcadena*/
    $indice: Str-index("Hola Pepe", "Pe");/* 6*/

    /*Para insertar una subcadena en una cadena */
    $nuevoString: str-insert($string: "Hola Pepe", $insert: ", soy", $index: 5);
    $nuevoString: str-insert("Hola Pepe", ", soy", 5);
    /*Para obtener la longitud de cadena*/
    $cadena: "Musica";
    $longitud: str-length($string: $cadena); /* 6 */

    /*Slice: para extraer una subcadena*/
    $cad-cortada: str-slice("Hola Pepe", 3, 7); /* "la Pe" */

    /*Para convertir una cadena a mayusculas/minusculas */
    $res: to-upper-case("Hola a Todos"); /*HOLA A TODOS" */
    $res: to-lower-case("Hola a Todos"); /*hola a todos" */

    $red: 44;
    color:rgb($green: 156, $red: $red, $blue: 225);
}

p{
    /*Valor absoluto de un numero */
    $n: abs(-20); /* 20 */

    /*Ceil: redondeo al entero superior */
    $n: ceil(15.45); /* 16 */

    /*floor: redondeo al entero inferior */
    $n: floor(15.99); /* 15 */

    /* Para comparar dos numeros */
    $resultado: comparable(15px , 9px);/* true */
    $resultado: comparable(100mm , 5cm);/* true */
    $resultado: comparable(35px , 85px);/* false */
    
    /*maximo/minimo*/
    $val: max(4, 6, 2, 48, -5, 0); /* 48 */
    $val: min(18, 7, 5, 255, 34,9); /* 5 */
    color: rgb($val, 0, 200);

    /* Porcentaje */
    /*
        0, 1, 0.5, 0.25
    */
    $val: percentage(1.3); /* 130 */
    $val: percentage(0.25); /* 25 */

    $val: random(); /* 0.45683 */
    $val: random(); /* 0.9815 */
    $val: random(6); /* 1 y 6 (inclusivo) */
}




























































































